'use client';

import { useState } from 'react';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

// Dummy forum categories
const forumCategories = [
  {
    id: 'image-generation',
    name: 'توليد الصور',
    description: 'نقاشات حول تقنيات توليد الصور باستخدام الذكاء الاصطناعي',
    topics: 24,
    lastPostTime: '15 دقيقة',
  },
  {
    id: 'llm',
    name: 'نماذج اللغة الكبيرة',
    description: 'مناقشات حول ChatGPT وLlama وBard وغيرها من نماذج اللغة',
    topics: 42,
    lastPostTime: '30 دقيقة',
  },
  {
    id: 'audio-generation',
    name: 'توليد الصوت',
    description: 'استخدام الذكاء الاصطناعي لإنشاء أصوات ونصوص منطوقة',
    topics: 18,
    lastPostTime: 'ساعة واحدة',
  },
  {
    id: 'video-generation',
    name: 'توليد الفيديو',
    description: 'تقنيات إنشاء وتحرير الفيديو بمساعدة الذكاء الاصطناعي',
    topics: 16,
    lastPostTime: 'ساعتين',
  },
  {
    id: 'coding',
    name: 'البرمجة والتطوير',
    description: 'مساعدة الذكاء الاصطناعي في كتابة الأكواد وتطوير البرامج',
    topics: 37,
    lastPostTime: '3 ساعات',
  },
  {
    id: 'comfy-ui',
    name: 'Comfy UI',
    description: 'نصائح وحلول لاستخدام ComfyUI بشكل احترافي',
    topics: 29,
    lastPostTime: '6 ساعات',
  },
];

export default function ForumPage() {
  return (
    <>
      <Navbar />
      <div className="bg-gray-50 py-12">
        <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h1 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
              منتدى الخبرة
            </h1>
            <p className="mt-3 max-w-2xl mx-auto text-xl text-gray-500 sm:mt-4">
              مساحة للنقاش وتبادل الخبرات بين المتخصصين في مجالات الذكاء الاصطناعي المختلفة
            </p>
          </div>
          
          <div className="bg-white shadow overflow-hidden sm:rounded-md">
            <div className="border-b border-gray-200 bg-gray-50 px-4 py-5 sm:px-6">
              <h3 className="text-lg font-medium leading-6 text-gray-900">الأقسام</h3>
            </div>
            <ul role="list" className="divide-y divide-gray-200">
              {forumCategories.map((category) => (
                <li key={category.id}>
                  <a href={`/forum/${category.id}`} className="block hover:bg-gray-50">
                    <div className="px-4 py-4 sm:px-6">
                      <div className="flex items-center justify-between">
                        <p className="text-lg font-medium text-indigo-600 truncate">
                          {category.name}
                        </p>
                        <div className="mr-5 flex-shrink-0 flex">
                          <p className="inline-flex px-2 text-xs font-semibold rounded-full bg-green-100 text-green-800">
                            {category.topics} موضوع
                          </p>
                        </div>
                      </div>
                      <div className="mt-2 sm:flex sm:justify-between">
                        <div className="sm:flex">
                          <p className="flex items-center text-sm text-gray-500">
                            {category.description}
                          </p>
                        </div>
                        <div className="mt-2 flex items-center text-sm text-gray-500 sm:mt-0">
                          <p>
                            آخر مشاركة منذ {category.lastPostTime}
                          </p>
                        </div>
                      </div>
                    </div>
                  </a>
                </li>
              ))}
            </ul>
          </div>
          
          <div className="mt-12 text-center">
            <div className="inline-flex items-center px-4 py-2 border border-amber-300 text-amber-700 bg-amber-100 rounded-md">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="h-5 w-5 ml-2"
                viewBox="0 0 20 20"
                fill="currentColor"
              >
                <path
                  fillRule="evenodd"
                  d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z"
                  clipRule="evenodd"
                />
              </svg>
              <span>ملاحظة: المنتدى متاح فقط للأعضاء المسجلين في المنصة</span>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
} 