'use client';

import { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/outline';
import NotificationsDropdown from './NotificationsDropdown';

const navigation = [
  { name: 'المعرض', href: '/gallery' },
  { name: 'طلب خدمة', href: '/service-request' },
  { name: 'المنتدى', href: '/forum' },
  { name: 'المساعدة', href: '/help' },
];

export default function Navbar() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const pathname = usePathname();
  const [isLoggedIn, setIsLoggedIn] = useState(true); // تغيير هذا لاحقًا للتحقق من حالة تسجيل الدخول

  return (
    <header className="bg-white shadow-sm">
      <nav className="mx-auto flex max-w-7xl items-center justify-between p-6 lg:px-8" aria-label="Global">
        <div className="flex lg:flex-1">
          <Link href="/" className="-m-1.5 p-1.5">
            <span className="sr-only">عَرَبَاي</span>
            <h1 className="text-2xl font-bold bg-gradient-to-r from-indigo-600 to-emerald-500 bg-clip-text text-transparent">
              عَرَبَاي <span className="text-xl">ArabAi</span>
            </h1>
          </Link>
        </div>
        <div className="flex lg:hidden">
          <button
            type="button"
            className="-m-2.5 inline-flex items-center justify-center rounded-md p-2.5 text-gray-700"
            onClick={() => setMobileMenuOpen(true)}
          >
            <span className="sr-only">فتح القائمة</span>
            <Bars3Icon className="h-6 w-6" aria-hidden="true" />
          </button>
        </div>
        <div className="hidden lg:flex lg:gap-x-12">
          {navigation.map((item) => (
            <Link
              key={item.name}
              href={item.href}
              className={`text-sm font-semibold leading-6 ${
                pathname === item.href
                  ? 'text-indigo-600'
                  : 'text-gray-900 hover:text-indigo-600'
              }`}
            >
              {item.name}
            </Link>
          ))}
        </div>
        <div className="hidden lg:flex lg:flex-1 lg:justify-end lg:gap-x-6">
          {isLoggedIn ? (
            <>
              <NotificationsDropdown />
              <Link href="/messages" className="text-sm font-semibold leading-6 text-gray-900 hover:text-indigo-600">
                الرسائل
              </Link>
              <Link href="/profile" className="text-sm font-semibold leading-6 text-gray-900 hover:text-indigo-600">
                الملف الشخصي
              </Link>
              <Link href="/payments" className="text-sm font-semibold leading-6 text-gray-900 hover:text-indigo-600">
                المدفوعات
              </Link>
            </>
          ) : (
            <Link href="/auth/login" className="text-sm font-semibold leading-6 text-gray-900 hover:text-indigo-600">
              تسجيل / دخول
            </Link>
          )}
        </div>
      </nav>
      
      {/* Mobile menu */}
      {mobileMenuOpen && (
        <div className="fixed inset-0 z-50 bg-gray-900/80 backdrop-blur-sm">
          <div className="fixed inset-y-0 left-0 right-0 z-50 w-full overflow-y-auto bg-white px-6 py-6">
            <div className="flex items-center justify-between">
              <Link href="/" className="-m-1.5 p-1.5">
                <span className="sr-only">عَرَبَاي</span>
                <h1 className="text-2xl font-bold bg-gradient-to-r from-indigo-600 to-emerald-500 bg-clip-text text-transparent">
                  عَرَبَاي <span className="text-xl">ArabAi</span>
                </h1>
              </Link>
              <button
                type="button"
                className="-m-2.5 rounded-md p-2.5 text-gray-700"
                onClick={() => setMobileMenuOpen(false)}
              >
                <span className="sr-only">إغلاق القائمة</span>
                <XMarkIcon className="h-6 w-6" aria-hidden="true" />
              </button>
            </div>
            <div className="mt-6 flow-root">
              <div className="-my-6 divide-y divide-gray-500/10">
                <div className="space-y-2 py-6">
                  {navigation.map((item) => (
                    <Link
                      key={item.name}
                      href={item.href}
                      className={`-mx-3 block rounded-lg px-3 py-2 text-base font-semibold leading-7 ${
                        pathname === item.href
                          ? 'text-indigo-600'
                          : 'text-gray-900 hover:text-indigo-600'
                      }`}
                      onClick={() => setMobileMenuOpen(false)}
                    >
                      {item.name}
                    </Link>
                  ))}
                  {isLoggedIn && (
                    <>
                      <Link
                        href="/messages"
                        className="-mx-3 block rounded-lg px-3 py-2 text-base font-semibold leading-7 text-gray-900 hover:text-indigo-600"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        الرسائل
                      </Link>
                      <Link
                        href="/profile"
                        className="-mx-3 block rounded-lg px-3 py-2 text-base font-semibold leading-7 text-gray-900 hover:text-indigo-600"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        الملف الشخصي
                      </Link>
                      <Link
                        href="/payments"
                        className="-mx-3 block rounded-lg px-3 py-2 text-base font-semibold leading-7 text-gray-900 hover:text-indigo-600"
                        onClick={() => setMobileMenuOpen(false)}
                      >
                        المدفوعات
                      </Link>
                    </>
                  )}
                  {!isLoggedIn && (
                    <Link
                      href="/auth/login"
                      className="-mx-3 block rounded-lg px-3 py-2 text-base font-semibold leading-7 text-gray-900 hover:text-indigo-600"
                      onClick={() => setMobileMenuOpen(false)}
                    >
                      تسجيل / دخول
                    </Link>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </header>
  );
} 