'use client';

import { useState, Fragment } from 'react';
import { Menu, Transition } from '@headlessui/react';
import { BellIcon } from '@heroicons/react/24/outline';

// بيانات وهمية للإشعارات
const notifications = [
  {
    id: 1,
    title: 'طلب خدمة جديد',
    message: 'قام محمد علي بطلب خدمة توليد صور جديدة',
    time: 'منذ 5 دقائق',
    read: false,
    type: 'request',
  },
  {
    id: 2,
    title: 'تم تنفيذ طلبك',
    message: 'تم الانتهاء من طلبك لتوليد فيديو',
    time: 'منذ 30 دقيقة',
    read: false,
    type: 'completion',
  },
  {
    id: 3,
    title: 'رسالة جديدة',
    message: 'لديك رسالة جديدة من أحمد محمود',
    time: 'منذ ساعة',
    read: true,
    type: 'message',
  },
  {
    id: 4,
    title: 'تم الدفع',
    message: 'تم إضافة 150$ إلى رصيدك',
    time: 'منذ يومين',
    read: true,
    type: 'payment',
  },
];

interface Notification {
  id: number;
  title: string;
  message: string;
  time: string;
  read: boolean;
  type: string;
}

export default function NotificationsDropdown() {
  const [notificationList, setNotificationList] = useState<Notification[]>(notifications);
  
  // تحديد عدد الإشعارات غير المقروءة
  const unreadCount = notificationList.filter(notification => !notification.read).length;
  
  // وضع علامة "مقروءة" على الإشعار
  const markAsRead = (id: number) => {
    setNotificationList(
      notificationList.map(notification =>
        notification.id === id ? { ...notification, read: true } : notification
      )
    );
  };
  
  // وضع علامة "مقروءة" على جميع الإشعارات
  const markAllAsRead = () => {
    setNotificationList(
      notificationList.map(notification => ({ ...notification, read: true }))
    );
  };

  return (
    <Menu as="div" className="relative inline-block text-right">
      <div>
        <Menu.Button className="relative inline-flex items-center justify-center p-2 rounded-md text-gray-500 hover:text-indigo-600 hover:bg-gray-100 focus:outline-none">
          <span className="sr-only">فتح قائمة الإشعارات</span>
          <BellIcon className="h-6 w-6" aria-hidden="true" />
          {unreadCount > 0 && (
            <span className="absolute top-0 left-0 h-4 w-4 rounded-full bg-red-500 flex items-center justify-center text-xs text-white">
              {unreadCount}
            </span>
          )}
        </Menu.Button>
      </div>
      
      <Transition
        as={Fragment}
        enter="transition ease-out duration-100"
        enterFrom="transform opacity-0 scale-95"
        enterTo="transform opacity-100 scale-100"
        leave="transition ease-in duration-75"
        leaveFrom="transform opacity-100 scale-100"
        leaveTo="transform opacity-0 scale-95"
      >
        <Menu.Items className="absolute left-0 mt-2 w-80 origin-top-left rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
          <div className="py-1">
            <div className="px-4 py-2 border-b border-gray-200 flex justify-between items-center">
              <h3 className="text-sm font-medium text-gray-900">الإشعارات</h3>
              {unreadCount > 0 && (
                <button
                  onClick={markAllAsRead}
                  className="text-xs text-indigo-600 hover:text-indigo-800"
                >
                  قراءة الكل
                </button>
              )}
            </div>
            
            <div className="max-h-60 overflow-y-auto">
              {notificationList.length > 0 ? (
                notificationList.map((notification) => (
                  <Menu.Item key={notification.id}>
                    {({ active }: { active: boolean }) => (
                      <button
                        onClick={() => markAsRead(notification.id)}
                        className={`${
                          active ? 'bg-gray-100' : ''
                        } block w-full text-right px-4 py-2 ${
                          !notification.read ? 'bg-indigo-50' : ''
                        }`}
                      >
                        <div className="flex flex-col">
                          <div className="flex justify-between items-start">
                            <span className="text-sm font-medium text-gray-900">
                              {notification.title}
                            </span>
                            <span className="text-xs text-gray-500 mr-2">
                              {notification.time}
                            </span>
                          </div>
                          <p className="text-xs text-gray-600 mt-1">
                            {notification.message}
                          </p>
                        </div>
                      </button>
                    )}
                  </Menu.Item>
                ))
              ) : (
                <div className="px-4 py-2 text-center text-sm text-gray-500">
                  لا توجد إشعارات
                </div>
              )}
            </div>
            
            <div className="border-t border-gray-200 px-4 py-2">
              <a
                href="/notifications"
                className="block text-center text-sm text-indigo-600 hover:text-indigo-800"
              >
                عرض كل الإشعارات
              </a>
            </div>
          </div>
        </Menu.Items>
      </Transition>
    </Menu>
  );
} 